import moment from "moment";
const useLineEchart = (data, desc) => {
  console.log("标准线数据", desc)
    return {
        tooltip: {
          trigger: 'axis',
          textStyle: {
            fontSize: 10
          },
          formatter:  function(params){
            console.log("formatter", params)
            return params.map(v => {
              let {seriesName, value } = v;
              let { standard } = desc.find(item => item.label == seriesName)?? {}
              return `${seriesName}:${value} ${ standard ? "标准值:" + standard : ""}</br>`;
            }).join("")
          }
        },
        legend: {
          textStyle: {
            fontSize: 10
          },
          itemHeight: 10,
          itemWidth: 10,
          bottom: 0,
          data: desc.map(item => item.label)
        },
        grid: {
          top: 6,
          left: '3%',
          right: '8%',
          bottom: '36',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: data.map(item => moment(item.time).format("MM/DD"))
        },
        yAxis: {
          type: 'value',
        },
        series: desc.length > 1 
          ? desc.map(item => ({
            name: item.label,
            type: "line",
            stack: 'Total',
            data: data.map(it => +it[item.key] || 0)
          })) 
          : [
            {
              type: "line",
              name: desc[0].label,
              data:  data.map(it => +it[desc[0].key] || 0), 
            }
          ]
      };
}

export default useLineEchart