const useDate = () => {
    function getWeekData(num){
        let date = new Date();
        return date.setDate(new Date().getDate() - (7 * Math.abs(num)))
    }

    function getMonthData(num){
        let date = new Date();
        return date.setMonth(new Date().getMonth() - Math.abs(num))
    }

    function getFullYearData(num){
        let date = new Date();
        return date.setFullYear(new Date().getFullYear() - Math.abs(num))
    }

    function getDayDate(num){
        let date = new Date();
        return date.setDate(new Date().getDate() - Math.abs(num))
    }
    return {
        getWeekData,
        getMonthData,
        getFullYearData,
        getDayDate
    }
}

export default useDate;