<template>
  <div class="history-data-bar" >
    <div class="select-time" >
      <div @click="changeCurrentIndex('prev')" class="change-time" >
        <svg aria-hidden="true" style="height: 44px;width: 9px;justify-content: center;align-items: center">
          <use xlink:href="#icon-goback"></use>
        </svg>
      </div>
      <div class="time-cont" >{{ timeFormat }}</div>
      <div @click="changeCurrentIndex('next')" style="transform: rotate(180deg);" class="change-time" >
        <svg aria-hidden="true" style="height: 44px;width: 9px;justify-content: center;align-items: center">
          <use xlink:href="#icon-goback"></use>
        </svg>
      </div>
    </div>
    <div>
      <slot name="inner" ></slot>
    </div>
  </div>
</template>

<script>
import {reactive, watchEffect, computed, ref, onBeforeMount, watch} from "vue";
  import moment from "moment";
  import { useDate } from "../utils/hooks";
  export default {
    props:{
      timeType: {
        type: String,
        required: true
      }
    },
    emits:[
      "selectTime"
    ],
    setup(props, {emit}){
      let time = reactive([]);
      let currentIndex = ref(-1);
      let {getWeekData, getMonthData, getFullYearData, getDayDate} = useDate();
      onBeforeMount(timeMethod)
      watch(() => props.timeType, timeMethod)

      function timeMethod(){
        let dateJson = {
          week: getWeekData,
          month: getMonthData,
          fullYear: getFullYearData,
          day: getDayDate
        }
        if(props.timeType === "day"){
          time[0] = dateJson[props.timeType ?? "week"]?.(currentIndex.value);
        }else{
          time[0] = dateJson[props.timeType ?? "week"]?.(currentIndex.value);
          time[1] = dateJson[props.timeType ?? "week"]?.(currentIndex.value + 1);
        }
        emit("selectTime", time)
      }

      let timeFormat = computed(() => time.map(item => moment(item).format("YYYY/MM/DD")).join("-"));

      function changeCurrentIndex (type) {
        if(type === "prev"){
          --currentIndex.value
          console.log(currentIndex.value)
        } else {
          currentIndex.value < -1 && ++currentIndex.value
        }
      };
      return {
        timeFormat,
        changeCurrentIndex
      };
    }
  }
</script>

<style lang="less" scoped>
  .history-data-bar{
    background: #fff;
    padding: 10px;
    border-radius: 5px;
    .select-time{
      height: 60px;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0 10px;
      .time-cont{
        flex: 1;
        font-size: 14px;
        color: #00B28B;
        text-align: center;
      }
      .change-time{
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
</style>
